
.bt {
    border-top-style: solid;
    border-top-width: 1px;
}

.bb {
    border-bottom-style: solid;
    border-bottom-width: 1px;
}

.b--black-10 {
    border-color: rgba(0, 0, 0, .1);
}

.mv1-l {
    margin-top: var(--spacing-extra-small);
    margin-bottom: var(--spacing-extra-small);
}

.db {
    display: block;
}

.flex {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.avenir {
    font-family: 'avenir next', avenir, sans-serif;
}

.baskerville {
    font-family: baskerville, serif;
}

.fw1 {
    font-weight: 100;
}

.lh-title {
    line-height: 1.25;
}

.lh-copy {
    line-height: 1.5;
}

.mw7 {
    max-width: 48rem;
}

.w-100 {
    width: 100%;
}

.black {
    color: #000;
}

.pv4 {
    padding-top: 2rem;
    padding-bottom: 2rem;
}

.ph3 {
    padding-left: 1rem;
    padding-right: 1rem;
}

.mb4 {
    margin-bottom: 2rem;
}

.mt0 {
    margin-top: 0;
}

.mv0 {
    margin-top: 0;
    margin-bottom: 0;
}

.no-underline {
    text-decoration: none;
}

.f3 {
    font-size: 1.5rem;
}

.f6 {
    font-size: .875rem;
}

.center {
    margin-right: auto;
    margin-left: auto;
}

.shadow-1 {
    box-shadow: 0px 0px 4px 2px rgba( 0, 0, 0, 0.2);
}

.dim {
    opacity: 1;
    transition: opacity .15s ease-in;
}

.dim:hover, .dim:focus {
    opacity: .5;
    transition: opacity .15s ease-in;
}

.dim:active {
    opacity: .8;
    transition: opacity .15s ease-out;
}

@media screen and (min-width: 30em) {

  
   
    .flex-row-ns {
        flex-direction: row;
    }

    .w-40-ns {
        width: 40%;
    }
    .w-60-ns {
        width: 60%;
    }
    .pl3-ns {
        padding-left: 1rem;
    }
    .pr3-ns {
        padding-right: 1rem;
    }
    .mb0-ns {
        margin-bottom: 0;
    }
}

@media screen and (min-width: 60em) {
    
    .ph0-l {
        padding-left: 0;
        padding-right: 0;
    }
    .f5-l {
        font-size: 1rem;
    }
}

@media screen and (max-width: 426px){
    .pic{
        display: none;
    }
    .info{
        text-align: center;
    }
}